import React, { useState, useEffect } from 'react';
// import Speech from 'react-speech';
import { useSpeechSynthesis } from 'react-speech-kit';
import Stats from "./stats"
import { FaArrowAltCircleRight } from 'react-icons/fa';


export default function Question({ ...data }) {
    const [isEnd, setEnd] = useState(false);
    const [isCorrect, setCorrect] = useState(false);
    const [chosenIdx, setChosenIdx] = useState(false);
    const [stats, setStats] = useState({ correct: 0, wrong: 0, questLeft: data.questLeft });
    const [isLock, setLock] = useState(false);
    // const onEnd = () => {
    //     console.log(' TALKER OVER!')
    //     cancel()

    // };
    // const { speak, cancel, speaking, supported, voices } = useSpeechSynthesis({
    //     onEnd,
    // });


    // async function talkBitch() {
    //     console.log('TALKING!')
    //     speak({ text: data.data.quiz.question })

    // }






    function onAnswer(idx) {
        if (isLock) return
        console.log('answerd: ', idx)
        const answer = data.data.quiz.options[idx].slice(0, 1)
        const correctAnswer = data.data.quiz.answers.slice(8, 9)
        setCorrect(getRightAnswerIdx(correctAnswer))
        let newStats = { ...stats }
        if (answer === correctAnswer) {
            setChosenIdx(idx)
            newStats.correct += 1
        } else {
            setChosenIdx(idx)
            newStats.wrong += 1
        }
        setStats(newStats)
        setEnd(1)
        setLock(true)
        console.log('STATS: ', stats)
    }

    function getRightAnswerIdx(correctAnswer) {
        let correctIdx = false
        data.data.quiz.options.forEach((opt, idx) => {
            if (opt.slice(0, 1) === correctAnswer) {
                correctIdx = idx
            }
        })
        return correctIdx
    }
    function getBoxClass(idx) {
        if (!isEnd) return
        if (isCorrect === idx) {
            return 'answer-box-correct'
        } else if (idx === chosenIdx) {
            return 'answer-box-wrong'
        }
    }
    function goNext() {
        data.onGoNext()
        setEnd(false);
        setCorrect(false);
        setChosenIdx(false);
        setLock(false)
        const newStats = { ...stats }
        newStats.questLeft = data.questLeft
        setStats(newStats)
        // talkBitch()
    }
    return (<>
        <Stats stats={stats}></Stats>
        <div className='questions'>
            {/* <Speech text={data.data.quiz.question} voice="Daniel" /> */}

            <h1 className="question-title">{data.data.quiz.question}</h1>
            <div className="answer-container">
                {/* <button
                    type="button"
                    onClick={() => talkBitch()}
                >
                    Stupid Robot
              </button> */}



                <div className="answer-box-grid">
                    {data.data.quiz.options.map((opt, idx) => {
                        return (<div key={opt + idx} onClick={() => onAnswer(idx)} className={`answer-box ${getBoxClass(idx)}`}><p>{opt}</p></div>)
                    })}

                </div>
            </div>


        </div>
        <div className="next-question-btn" onClick={goNext}><FaArrowAltCircleRight></FaArrowAltCircleRight></div></>
    );
}
