import React, { useEffect, useState } from 'react';
import Questions from "./question"
import dataJSON from "../data/questions.json"
import best from "../data/best.mp3"
export default function Quiz() {

    const [currentQuestionIdx, setCurrentQuestionIdx] = useState(0);
    const [showScore, setShowScore] = useState(false);
    const [score, setScore] = useState(0);
    const [data, setData] = useState(dataJSON);
    const [quiz, setQuiz] = useState(0);
    const [num, setNum] = useState(50);
    const [left, setLeft] = useState(quiz.length)
    const bestAudio = new Audio(best)



    // const handleAnswerOptionClick = (isCorrect) => {
    // 	if (isCorrect) {
    // 		setScore(score + 1);
    // 	}

    // 	const nextQuestion = currentQuestion + 1;
    // 	if (nextQuestion < questions.length) {
    // 		setCurrentQuestion(nextQuestion);
    // 	} else {
    // 		setShowScore(true);
    // 	}
    // };
    // console.log("Data", data)
    function getRandom(arr, n) {
        var result = new Array(n),
            len = arr.length,
            taken = new Array(len);
        if (n > len)
            console.log("getRandom: more elements taken than available");
        while (n--) {
            var x = Math.floor(Math.random() * len);
            result[n] = arr[x in taken ? taken[x] : x];
            taken[x] = --len in taken ? taken[len] : len;
        }
        return result;
    }

    function getQuiz(number) {
        const questions = getRandom(data.data, number)
        console.log("ðŸš€ ~ file: quiz.js ~ line 42 ~ getQuiz ~ questions", questions)
        setQuiz(questions)
        setLeft(questions.length)
    }



    function getCurrQuest(idx) {

        console.log("QUIZZZ", quiz[idx])
        return quiz[idx]
    }
    function playBest() {
        console.log('PLAYING!')
        bestAudio.play()
    }

    function goNext() {
        if (currentQuestionIdx === quiz.length - 1) return
        setCurrentQuestionIdx(currentQuestionIdx + 1)
        const res = quiz.length - 1 - currentQuestionIdx
        setLeft(res)
        console.log("quiz.length", quiz.length, "currentQuestionIdx", currentQuestionIdx)


    }




    return (currentQuestionIdx === quiz.length - 1 ? (
        <div className="done-container">
            <h1>Good Job! Try Again!</h1>
            <button onClick={() => { window.location.href = window.location.href }}>Restart!</button>
            { playBest()}
            <div className="fireworks-container">

                <div class="pyro">
                    <div class="before"></div>
                    <div class="after"></div>
                </div>
            </div>
        </div>

    ) :
        <div className='questions'>
            <div className="start-quiz-container">
                <h1>CEH training Quiz!<br></br><br></br>Choose Questions Amount!</h1>
                <input
                    className="quiz-input"
                    type="number"
                    placeholder="Set Quiz Number"
                    value={num}
                    max="350"
                    onChange={e => setNum(e.target.value)}></input>
                <button onClick={() => getQuiz(num)}>Start!</button>
            </div>


            {
                quiz.length > 0 && <Questions data={{ quiz: getCurrQuest(currentQuestionIdx) }} onGoNext={goNext} questLeft={left}></Questions>
            }
        </div >

    );
}
