import React, { useState } from 'react';

export default function Stats({ stats }) {

    return (
        <div className='stats-container'>
            <h1 className="stats-correct">Correct: {stats.correct}</h1>
            <h1 className="stats-wrong">Wrong: {stats.wrong}</h1>
            <h1 className="stats-wrong">Left: {stats.questLeft}</h1>
        </div>
    );
}
